function strip(html){
    var doc = new DOMParser().parseFromString(html, 'text/html');
    return doc.body.textContent || "";
 }
export default async function changeLanguage(set,theRef){
    let lang = prompt('language');
    let res = await (await fetch('https://'.concat(lang.concat('.wikipedia.org')),{mode: 'cors'})).text();
    theRef.current.innerText = 'click to continue' + strip(res);
    let confirmed = await new Promise(c => {setTimeout(c.bind(null,false),4000); theRef.current.addEventListener('click',function a(){c(true); theRef.current.removeEventListener('click',a)})})
if(confirmed)set(lang)
    }